  function handleChangeEmail(e) {
    setEmail(e.target.value);
    setMessageStatus("");
    const emailRegex = /^([\w]+@([\w-]+\.)+[\w-]{2,4})?$/;

    if (e.target.value.length === 0) {
      setEmailError("    ");
      setEmailValid(false);
    } else if (!emailRegex.test(String(e.target.value).toLocaleLowerCase())) {
      setEmailError(" email");
      setEmailValid(false);
    } else {
      setEmailError("");
      setEmailValid(true);
    }
  }

